@-webkit-keyframes popDown {
  0% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
}

@keyframes popDown {
  0% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
}

@-webkit-keyframes popUp {
  100% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
}

@keyframes popUp {
  100% {
    -webkit-transform: translateY(-100px);
            transform: translateY(-100px);
  }
}

.topbar{
  left:0;
  height: 50px;
  min-height: 50px;
  background-color: #fff;
  width:100%;
  text-align:center;
  font-size:22px;
  font-family: 'Raleway';
  box-shadow: 0px 4px 4px rgba(0,0,0,0.2);
  -webkit-transition: top .3s;
  transition: top .3s;
  z-index: 10;
}

.non-stick{
  position: absolute;
  bottom: -50px;
}

.stick-top{
  position: fixed;
  top:0vh;
  -webkit-animation: popDown .75s;
          animation: popDown .75s;
}

/* Navbar Styling */
.navbar {
  font-size: 15px;
  height:100%;
  /* background-image: linear-gradient(260deg, #2376ae 0%, #c16ecf   100%); */
  background-image: linear-gradient(260deg, #1b242f 0%, #1b242f 100%);
  border: 1px solid rgba(0, 0, 0, 0.2);
  bottom: 0px;
  z-index: 5;
  border-bottom: 3px solid orange;
}
.link-wrap {
  list-style-type: none;
  display: none;
}

.link-wrap li {
  text-align: center;
  margin: 15px auto;
}

.nav-links,
.logo {
  text-decoration: none;
  color: rgba(255, 255, 255, 0.7);
}                   
.logo {
  display: none;
  font-size: 22px;
  margin-top: 10px;
  margin-left: 20px;
}

.hb-show{
  display: block;
  height: 110px;
  -webkit-transition: height .25s;
  transition: height .25s;
}

.hb-hide{
  display: block;
  height: 0px;
  -webkit-transition: height .25s;
  transition: height .25s;
}

.hb-img {
  height: 70%;
}

.hb-img:hover {
  opacity: .50;
}

.hb-button {
  display: flex;
  justify-content: flex-end;
  padding-right: 15px;
  height: 100%;
  align-items: center;
}

.hb-menu {
  display: block;
  background-color: gray;
  -webkit-animation: popDown;
          animation: popDown;
  z-index: -1;
  overflow: hidden;
}  

.hb-menu-close {
  display: block;
  background-color: gray;
  -webkit-animation: popDown;
          animation: popDown;
  z-index: -1;
  height: 0px;
  -webkit-transition: 2s;
  transition: 2s;
}  

@media screen and (min-width: 600px) {
  .navbar {
    display: flex;
    justify-content: space-between;
    padding-bottom: 0;
    height: 100%;
    width: 100%;
    align-items: center;
    position: static;
    bottom: 0;
    right: 0;
  }
  .link-wrap {
    display: flex;
    margin-right: 30px;
    flex-direction: row;
    justify-content: flex-start;
  }
  .link-wrap li {
    margin: 0;
  }
  .nav-links {
    margin-left: 40px;
    cursor: pointer;
  }
  .nav-links, .logo{
    display: block;
  }

  .logo {
    margin-top: 0;
   }
  /* .navbar-toggle {
    display: none;
  } */
  .logo:hover,
  .nav-links:hover {
    color: rgba(255, 255, 255, 1);
  }

  .nav-links:active {
    opacity: .5;
  }

  .hb-button {
    display: none;
  }
  .hb-menu {
    display: none;
  }  
}
